.header {
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.headerPhoto{
    display: inline-block;
    border-radius: 100%;
    margin-right: 10px;
}
.button_header{
    border-radius: 5px;
    transition: .5s linear;
    cursor: pointer;
}
.button_header:hover{
    background-color: gold;
}
.header_name_profile{
    color:white;
}
.header img {
    width: 50px;
    height: 50px;
}

.loginBlock {
    display: flex;
    justify-content: space-around;
    align-items: center;
}

