.item a{
    align-items: center;
    border-radius: 5px;
    border: 2px solid #e6e7ee;
    color: #1c4f7b;
    cursor: pointer;
    display: flex;
    font-size: 25px;
    font-style: normal;
    font-weight: 600;
    margin: 0 0 0.3rem;
    padding: 1rem 0 1rem 1.5rem;
    transition: .1s ease-in-out;
    text-decoration: none;
    font-weight: 400;
    font-style: normal;
    font-size: 16px;
    font-family: "Poppins", sans-serif;
}
.item a:hover{
    border: 2px solid #63a4dc;
    box-shadow: 2px 2px 9px rgba(0,0,0,.25);
    color: #63a4dc;
}
.item a.active {
    color:#fff;
    background-color: #63a4dc;
}
.routesImg{
    width: 30px;
    height: 30px;
    margin-right: 30px;
}